import React from "react";
import { Card, WingBlank, WhiteSpace, List } from "antd-mobile";

//less引入
import "./StudentsMessage.less";

const Item = List.Item;
const Brief = Item.Brief;
export default class Demo extends React.Component {
  state = {
    disabled: false
  };
  componentDidMount() {
    this.props.changeTitle("学生相关信息");
  }
  render() {
    return (
      <div>
        <WingBlank size="sm">
          <WhiteSpace size="sm" />
          <Card>
            <Card.Header
              thumb="http://img1.touxiang.cn/uploads/20120816/16-004551_548.jpg"
              thumbStyle={{ width: "60%" }}
              extra={
                <div>
                  <span>王小二</span>
                  <h6>三年二班</h6>
                </div>
              }
            />
            <Card.Body>
              <Item>年龄：8岁</Item>
              <Item>身高：130cm</Item>
              <Item>体重：30kg</Item>
            </Card.Body>
            <Card.Footer />
          </Card>
          <WhiteSpace size="sm" />
        </WingBlank>
        <List
          renderHeader={() => "相关信息"}
          extra={<span>查看更多...</span>}
          className="my-list"
        >
          <Item arrow="horizontal" multipleLine onClick={() => {}}>
            测试成绩 <Brief>12月进行考试成绩</Brief>
          </Item>
          <Item
            arrow="horizontal"
            multipleLine
            onClick={() => {}}
            platform="cross"
          >
            昨日饮食
            <Brief>饮食搭配，食材为：肉，小白菜，鱼，青菜，黄花菜</Brief>
          </Item>
          <Item arrow="horizontal" multipleLine onClick={() => {}}>
            作业完成 <Brief>完成作业情况，对于数学作业完成度较为困难</Brief>
          </Item>
        </List>
      </div>
    );
  }
}



